import * as React from "react"

import Layout from "../../components/layout"
import Seo from "../../components/seo"
import "./volunteer.css"

const volunteer = () => (
    <Layout page="volunteer">
      <Seo title="Volunteer" description="If your organization is looking for volunteers, please drop down your message here." />
      <section className="section">
        <div className="widthLimit-volunteer">
          <div className="columns">
            <div className="column is-two-fifths">
              <div className="volunteer-innerTextContainer">
                <strong className="volunteer-title">We are willing to help!</strong>
                <span className="volunteer-description">If your organization is looking for volunteers, please drop down your message here or email us at <a className="volunteer-linkInDescription" href="mailto:volunteer@sfcis.org">volunteer@sfcis.org</a> directly.</span>
                <strong className="volunteer-textAfterDescription">We will reply within 3 business days.</strong>
              </div>
            </div>
            <div className="column">
              <iframe
                src="https://docs.google.com/forms/d/e/1FAIpQLSeeyJH1TliBQnZKOwHtGvf7PWZVovcBFvu0heUGhd_vEkss5Q/viewform?embedded=true"
                scrolling="no"
                title="Contact information"
                width="100%"
                height="1200"
                style={{
                  paddingTop: "24px"
                }}
              >Loadingâ€¦
              </iframe>
            </div>
          </div>
        </div>
      </section>
    </Layout>
)

export default volunteer
